.contacts {
	&__in {
		padding-bottom: 70px; }
	&__info {
		font-style: normal;
		@include clr;
		& + & {
			padding-top: 50px; } }
	&__info-data {
		float: left;
		width: percentage(530 / 1310);
		padding-top: 15px; }
	&__info-title {
		margin-bottom: 45px;
		font-size: 27px;
		text-align: center; }
	&__info-mail {
		margin-bottom: 5px; }
	&__info-mail a {
		color: #008CFF;
		text-decoration: underline;
		&:hover {
			text-decoration: none; } }
	&__info-map {
		position: relative;
		float: right;
		width: percentage(690 / 1310);
		&::after {
			content: '';
			@include coverdiv;
			z-index: 5;
			box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.4); } }
	&__info-map iframe {
		width: 100%;
		height: 450px; }
	&__info-item {
		margin-top: 25px;
		font-size: 20px; }
	&__info-text + &__info-text {
		margin-top: 35px; }

	&__socials {
		padding: 40px 0; }
	&__socials-links {
		@include f('psb', 22);
		text-align: center;
		a {
			color: #274F7F;
			text-decoration: underline;
			&:hover {
				text-decoration: none; } } }
	&__descr {
		padding: 35px 0;
		margin: 0 40px;
		border-top: 1px solid #D1D1D1;
		border-bottom: 1px solid #D1D1D1;
		font-size: 20px;
		&-title {
			margin-bottom: 40px;
			@include f('oss');
			text-align: center; }
		&-text {
			text-indent: 35px; } }

	&_default {
		padding-left: 30px;
		padding-bottom: 45px; } }

