.benefits {
	padding: 30px 0;
	background-color: #d8e7f9;
	@include lg {
		padding: 25px 0; }
	@include md {
		padding: 5px 0; }
	@include sm {
		padding: 5px 0 15px; }
	@include xs {
		padding: 0 0 10px; }
	&__container {
		display: flex;
		justify-content: space-between;
		max-width: 1370px;
		@include md {
			max-width: 1200px; } }
	&__item {
		display: flex;
		flex-basis: 33.33333%;
		align-items: center;
		&:nth-child(2) {
			padding-left: 20px;
			@include sm {
				padding-left: 0; } }
		&:nth-child(3) {
			padding-left: 40px;
			@include sm {
				padding-left: 0; } }
		&-pic {
			@include sm {
				width: 100px;
				height: 100px; }
			@include xs {
				position: relative;
				width: 80px;
				height: 80px; }
			&::after {
				content: '';
				display: block;
				transform: scale(.9);
				@include md {
					transform: scale(.67); }
				@include xs {
					transform: scale(.55);
					position: relative;
					top: -9px;
					left: -23px; } }
			&_pic-1::after {
				@include s('benefit_1'); }
			&_pic-2::after {
				@include s('benefit_2'); }
			&_pic-3::after {
				@include s('benefit_3'); } }
		&-text {
			padding-left: 15px;
			padding-right: 10px;
			font-size: 20px;
			line-height: (26/20);
			color: #595858;
			@include lg {
				font-size: 20px; }
			@include md {
				font-size: 15px; }
			@include sm {
				margin-top: 15px;
				font-size: 14px;
				padding-left: 10px;
				line-height: 1.2; }
			@include xs {
				padding-left: 0;
				padding-right: 0;
				font-size: 13px; } } }
	&_default {
		margin-top: 50px; } }
