/* links
	----------------------------------- */
.link-decor {
	color: #008CFF;
	span {
		padding-bottom: 3px;
		background-image: linear-gradient(to right, #008CFF 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #008CFF 16%, rgba(255, 255, 255, 0) 0%);
		background-size: 22px 2px, 6px 2px;
		background-repeat: repeat-x;
		background-position: bottom; }
	&:hover span {
		color: #0051FF;
		background-image: linear-gradient(to right, darken(#008CFF, 10) 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, darken(#008CFF, 10) 16%, rgba(255, 255, 255, 0) 0%); }
	&_rubric {
		line-height: 1.35;
		span {
			background-image: linear-gradient(to right, #B3B3B3 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #B3B3B3 16%, rgba(255, 255, 255, 0) 0%); }
