	&:hover span {
		color: darken(#FCBD7E, 15);
		border-bottom-color: darken(#FCBD7E, 15); } }

/* buttons
	----------------------------------- */
.btn {
	position: relative;
	display: inline-block;
	min-width: 340px;
	padding: 16px 70px 16px 5px;
	@include f("psr",22);
	color: $white;
	outline: none;
	text-decoration: none;
	text-align: center;
	background-color: #52C0E9;
	border-bottom: 1px solid #E7E7E7;
	@include lg {
		min-width: 280px;
		font-size: 20px;
		padding: 13px 55px 13px 5px; }
	@include md {
		min-width: 238px;
		padding: 11px 41px 11px 5px;
		font-size: 17px; }
	@include sm {
		min-width: 210px;
		padding: 6px 34px 6px 5px;
		font-size: 15px; }
	&::after {
		content: '';
		position: absolute;
		top: calc(50% - 18px);
		right: 32px;
		z-index: 1;
		@include s('btn_arr');
		transform: scale(.9);
		@include lg {
			right: 13px; }
		@include md {
			right: 10px;
			transform: scale(.7); }
		@include sm {
			right: 6px;
			transform: scale(.6); } }
	&:hover {
		background-color: #128FCD;
		border-bottom-color: #F9BF84; }
	&:hover::after {
		@include s('btn_arr-hvr'); }
	&:active {
		background-color: #24568D;
		border-bottom-color: #E63833; }
	&:active::after {
		@include s('btn_arr-act'); }
	&_dark {
		background-color: #1768BF;
		border-bottom-color: #F9BF84;
		&::after {
			@include s('btn_dark'); }
		&:hover {
			background-color: #00BBFF;
			border-bottom-color: #F9BF84; }
		&:hover::after {
			@include s('btn_dark-hvr'); }
		&:active {
			background-color: #24568D;
			border-bottom-color: #E63833; } } }

.send-btn {
	position: relative;
	padding: 0 70px 0 5px;
	min-width: 232px;
	line-height: 45px;
	height: 45px;
	color: #fff;
	background: #6ca5db;
	text-align: right;
	border-bottom: 2px solid #a7b8c9;
	@include f("ver",19);
	overflow: hidden;
	&::after {
		position: absolute;
		top: -4px;
		right: -4px;
		@include s('send');
		content: '';
		transform: scale(.87);
		@include md {
			top: -7px;
			right: -8px;
			transform: scale(.73); } }
	&:hover {
		background-color: #63afed;
		border-bottom-color: #ffff74; }
	&:hover::after {
		@include s('send-hvr'); }
	&:active {
		background-color: #415c89;
		border-bottom-color: #a95a63; }
	&:active::after {
		@include s('send-act'); } }

.btn-cart {
	position: relative;
	padding: 17px 90px 17px 50px;
	font-size: 21px;
	color: #fff;
	background: #2AB1E2;
	border-bottom: 2px solid #E3E3E3;
	overflow: hidden;
	&::after {
		position: absolute;
		top: -5px;
		right: -7px;
		@include sr('cart_add');
