	&:active::after {
		@include sr('cart_add-act'); } }

.wish-btn {
	position: relative;
	min-width: 230px;
	height: 37px;
	line-height: 37px;
	padding: 0 40px 0 10px;
	text-align: center;
	font-size: 19px;
