// pic decor
.decor {
	position: relative;
	background-color: #F4F8FD;
	&::after {
		position: absolute;
		z-index: 1;
		right: 0;
		bottom: 0;
		content: '';
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 0 20px 20px;
		border-color: transparent transparent #fff transparent; }
	&__line {
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 87%;
		height: 1px;
		background-color: rgba(#DAE5EC, .65);
		align-self: flex-end;
		&::before, &::after {
			position: absolute;
			z-index: 1;
			bottom: 0;
			content: '';
			transition: border-color .3s; }
		&::before {
			right: -16px;
			@include arr(15,20, rgba(#000, .25),br); }
		&::after {
			right: -39px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 22px 23px 0 0;
			border-color: rgba(238, 244, 251, 0.9) transparent transparent transparent;
			@include sm {
				right: -39px; } }
		&_suggest {
			width: 95%; } }
	&_sm {
		background-color: #D7DCE0;
		&::after {
			display: none; }
		.decor__line {
			width: 85%; }
		.decor__line:after {
			right: -20px;
			border-width: 19px 19px 0 0;
			background-color: #ececec; }
		.decor__line:before {
			right: -1px; } }

	&_xs {
		&::after {
			border-width: 0 0 20px 19px;
			right: -2px; }
		.decor__line {
			width: 80%;
			bottom: 0; }
		.decor__line::after {
			right: -26px;
			border-width: 18px 18px 0 0; }
		.decor__line::before {
			right: -8px;
			border-width: 0 0 18px 13px; } } }


/* breadcrumbs
	----------------------------------- */
.breadcrumbs {
	font-size: 0;
	&__link {
		display: inline-block;
		vertical-align: middle;
		font-size: 20px;
		span {
			display: inline-block;
			vertical-align: middle; }
		&::after {
			display: inline-block;
			vertical-align: middle;
			margin: 0 3px;
			content: '/'; }
		&:last-child::after {
			display: none; }
		&:hover {
			color: $hover; }
		&.is-active {
			pointer-events: none; } }
	&__link:hover::after {
		color: #000; }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 5px;
		fill: #D0CFCF; } }

