		margin: 0 5px;
		&_prev {
			margin-right: 10px;
			text-align: center;
			.pagination__list-link {
				padding: 0;
				line-height: 39px; } }
		&_dots {
			margin: 0 10px; } }
	&__list-item.is-active &__list-link {
			background: #FFEBAB; }
	&__list-link {
		display: inline-block;
		font-size: 19px;
		font-family: Arial, sans-serif;
		color: #667FA6;
		padding: 6px 12px;
