		height: 39px;
		min-width: 38px;
		&:hover {
			background: #EDEDED; }
		&:active {
			background: #C8D6E6; } }
	&__list-link:hover &__icon-arr {
		fill: lighten(#667FA6, 20); }
	&__icon-arr {
		display: inline-block;
		vertical-align: middle;
		fill: #EDEDED;
