/* sub-menu
	----------------------------------- */
.category-menu {
	position: relative;
	&::after {
		content: '';
		position: absolute;
		bottom: -1px;
		left: 5px;
		right: 5px;
		height: 1px;
		background: #D1D1D1;
		z-index: 1; }
	&__title {
		margin-bottom: 40px;
		text-align: center;
		color: #274F7F; }
	&__list {
		display: flex; }
	&__item {
		flex: 1 0 auto; }
	&__link {
		display: block;
		padding: 15px 5px;
		font-size: 19px;
		background: #F7FAFF;
		text-align: center;
		@include trs;
		&:hover {
			background: #E5F0FC; } }

	&_default {
		margin-top: auto; }
	&_basket, &_order {
		max-width: 1400px;
		margin: 50px auto 0; } }

/* rating
	----------------------------------- */
.rating {
	&__text {
		margin-bottom: 3px;
		color: #1C487A; }
	&__in {
		display: flex;
		flex-direction: row-reverse;
		justify-content: center; }
	&__input {
		display: none; }
	&__input ~ &__icon .rating__star {
		fill: #DBDBDB;
		stroke: #DBDBDB; }
	&__input:checked ~ &-icon .rating__star,
	&__input:checked &-icon .rating__star {
		fill: #FFED00;
		stroke: #FFED00; }
	&__icon:hover .rating__star,
	&__icon:hover ~ &__icon .rating__star {
		fill: #C7CBCF;
		stroke: #DBDBDB; }
	&__input:checked &__icon:hover .rating__star,
	&__input:checked ~ &__icon .rating__star {
		fill: #FFED00;
		stroke: #FFED00; }
	&__icon {
		display: inline-block;
		cursor: pointer; }
	&__star {
		font-size: 23px; } }

/* check
	----------------------------------- */
.check {
	&__circle {
		display: inline-block;
		margin-left: 4px;
		width: 24px;
		height: 24px;
		border: 2px solid #99AFCF;
		border-radius: 50%;
		text-align: center;
		line-height: 20px;
		&::after {
			display: inline-block;
			width: 14px;
			height: 14px;
			border-radius: 50%;
			background: #768FAE;
			content: '';
			transform: scale(.4);
			opacity: 0;
			transition: .15s; } }
	label {
		font-size: 19px;
		color: #000;
		cursor: pointer; }
	input {
		display: none; }
	input:checked + label .check__circle::after {
		transform: scale(1);
		opacity: 1; } }

/* label-block
	----------------------------------- */
.label-block {
	position: relative;
	&__title {
		position: absolute;
		top: 4px;
		left: 50%;
