		@include f('tcm', 16, 18);
		color: #004D8C;
		background: rgba(#DEF1FF, .6);
		text-transform: uppercase; }
	&__in {
		padding: 4px;
		border: 2px solid #47C1F0;
		@include clr; }
	&__item {
		position: relative;
		float: left;
		width: calc(50% - 5px);
		padding-top: 36%;
		margin-right: 5px;
		margin-bottom: 5px;
