.request {
	padding: 80px 0;
	background-color: #ececec;
	@include lg {
		padding: 50px 0; }
	@include md {
		padding: 35px 0 25px; }
	@include xs {
		padding: 20px 0 10px; }
	&__container {
		max-width: 1024px; }
	&__title {
		margin-bottom: 40px;
		text-align: center;
		@include lg {
			margin-bottom: 30px; }
		@include md {
			font-size: 19px; }
		@include xs {
			font-size: 17px; } }
	&__descr {
		text-align: center;
		font-size: 20px;
		@include md {
			font-size: 16px; }
		@include xs {
			font-size: 15px; } }
	&__link {
		color: #1166ab;
		border-bottom: 1px solid #1166ab;
		&:hover {
			border-bottom-color: transparent; } }
	&__wrap {
		margin: 40px 0 0;
		@include lg {
			margin: 30px 0 0; }
		@include md {
			margin: 20px 0 0; } }
	&__row {
		display: flex;
		justify-content: space-between;
		margin: 0 0 40px;
		@include xs {
			margin: 0 0 20px; } }
	&__field {
		position: relative;
		flex-basis: 31.26143%;
		border: 1px solid #5d8dcd;
		overflow: hidden;
		&::after {
			position: absolute;
			left: -1px;
			top: -2px;
			z-index: 1;
			content: '';
			@include md {
				left: -6px;
				top: -4px;
				transform: scale(.8); } }
		&_name::after {
			@include s('user_form'); }
		&_tel::after {
			@include s('phone_form'); }
		&_email::after {
			@include s('mail_form'); } }
	&__input {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		padding: 0 5px 0 65px;
		height: 35px;
		line-height: 35px;
		background-color: #fff;
		@include f("ver",15);
		@include placeholder-color(#999999);
		@include md {
			padding: 0 5px 0 50px;
			line-height: 30px;
			height: 30px;
			font-size: 13px; } }
	&__message-wrap {
		position: relative;
		padding-left: 50px;
		border: 1px solid #5d8dcd;
		background-color: #f3f4f4;
		overflow: hidden;
		@include md {
			padding-left: 40px; }
		&::after {
			content: '';
			position: absolute;
			z-index: 1;
			top: -3px;
			left: -3px;
			@include s('dots_form');
			@include md {
				top: -6px;
				left: -7px;
				transform: scale(.75); } } }
	&__message {
		display: block;
		padding: 9px 15px 15px 16px;
		min-height: 165px;
		width: 100%;
		background-color: #fff;
		resize: none;
		border: none;
		@include f("ver",15);
		@include placeholder-color(#999999);
		@include tr(min-height .3s);
		@include md {
			min-height: 140px;
			padding: 6px 12px 12px 8px;
			font-size: 13px; }
		&.extended {
			min-height: 230px;
			@include md {
				min-height: 220px; } }
		&-extend {
			position: absolute;
			width: 65px;
			height: 29px;
			background-color: #f7f7f7;
			bottom: 4px;
			right: 18px;
			line-height: 43px;
			&.is-active .request__message-icon {
				transform: scaleY(-1); } }
		&-extend:hover &-icon {
				fill: #56708A; }
		&-extend:active &-icon {
				fill: #103559; }
		&-icon {
			font-size: 21px;
			fill: #C4C4C4; } }
	&__send {
		margin-top: 20px;
		font-size: 0; }
	&__send-col {
		display: inline-block;
		vertical-align: top;
		font-size: 0; }
	&__send-col:nth-child(1) {
		width: percentage(600 / 994);
		white-space: nowrap;
		@include xs {
			width: 100%; } }
	&__send-col:nth-child(2) {
		padding-right: 10px;
		width: percentage(390 / 994);
		text-align: right;
		@include xs {
			width: 100%;
			margin-top: 20px;
			padding-right: 0;
			text-align: center; } }
	&__send-btn {
		@include md {
			padding: 0 73px 0 5px;
			min-width: 196px;
			font-size: 15px;
			line-height: 38px;
			height: 38px; } }
	&__send-add {
		display: inline-block;
		vertical-align: top;
		margin-right: 15px;
		white-space: nowrap; }
	&__send-text {
		display: inline-block;
		vertical-align: top;
		margin-left: 20px;
		margin-top: -3px;
		max-width: 160px;
		padding-left: 3px;
		@include f("ver",18);
		color: #040404;
		white-space: normal;
		@include md {
			font-size: 15px; }
		@include sm {
			font-size: 14px; } }
	&__send-btn-add {
		display: inline-block;
		vertical-align: middle;
		padding: 0 5px;
		max-width: 128px;
		min-height: 114px;
		color: #fff;
		background-color: #80C9E8;
		white-space: normal;
		@include f("ver",16);
		text-transform: uppercase;
		border-bottom: 1px solid #A8B3BF;
		@include lg {
			font-size: 13px;
			max-width: 108px;
			min-height: 96px; }
		&::after {
			content: '';
			display: block;
			margin: 3px auto 0;
			@include sr('file');
			transform: scale(.9);
			@include md {
				transform: scale(.8); } }
		&:hover::after {
			@include sr('file-hvr'); }
		&:active::after {
			@include sr('file-act'); }
		&:hover {
			background: #5EAFDA;
			border-bottom-color: #FFFF00; }
		&:active {
			background: #2A578A;
			border-bottom-color: #E63E2D; } }
	&__send-files {
		display: inline-block;
		vertical-align: bottom;
		font-size: 0;
		white-space: normal;
		width: percentage(280 / 600);
		@include xs {
			width: 59%; } }
	&__send-item {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		height: 116px;
		width: 130px;
		margin-right: 20px;
		@include vertical;
		@include md {
			width: 110px;
			height: 98px;
			margin-right: 15px; }
		&:last-child {
			margin-right: 0; }
		&:nth-child(2n) {
			margin-right: 0;
			@include xs {
				margin-right: 20px; } }
		&:nth-child(2) ~ & {
			margin-top: 20px;
			@include xs {
				margin-top: 0; } }
		&:nth-child(3) ~ & {
			@include xs {
				margin-top: 20px; } }
		img {
			display: inline-block;
			vertical-align: middle;
			width: 100%;
			height: 100%;
			object-fit: contain; }
		.decor__line {
			@include md {
				width: 82%; } }
		.decor__line::before {
			@include md {
				border-width: 0 0 16px 15px;
				right: -5px; } }
		.decor__line::after {
			@include md {
				border-width: 15px 15px 0 0; } } }
	&__send-btn-close {
		position: absolute;
		right: -15px;
		top: -15px;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background: #E63833;
		border: 3px solid #fff;
		@include tr(background .2s);
		@include md {
			width: 24px;
			height: 24px;
			border-width: 2px; }
		&:hover {
			background: #3e5b8a; } }
	&__send-icon-close {
		fill: #fff;
		@include vcenter;
		font-size: 12px;
		@include md {
			font-size: 10px; } }
	&__send-message {
		@include f("ver",20);
		@include md {
			font-size: 15px; }
		@include sm {
			font-size: 14px; }
		&::before {
			content: '';
			display: inline-block;
			vertical-align: -30px;
			transform: scale(.7);
			@include st('mark');
			@include md {
				transform: scale(.4); } } } }
