.footer {
	padding: 40px 0;
	background-color: #002757;
	background-image: radial-gradient(ellipse farthest-corner at center top, #002757 25%, rgba(0, 62, 138, 0.2) 100%);
	color: #fff;
	overflow: hidden;
	@include md {
		padding: 25px 0; }
	&__container {
		max-width: 1470px;
		@include clr; }
	&__col {
		position: relative;
		float: left;
		&:nth-child(1) {
			width: percentage(235 / 1440);
			@include md {
				margin-left: 50px; }
			@include sm {
				margin-left: 20px;
				width: 20%; }
			@include xs {
				width: 23%; } }
		&:nth-child(2) {
			width: 23.5%;
			@include md {
				width: 22%; }
			@include sm {
				display: none; } }
		&:nth-child(3) {
			width: 50%;
			float: right;
			@include md {
				width: 48%; }
			@include sm {
				width: 62%; } } }
	&__address {
		display: flex;
		font-style: normal;
		font-size: 19px;
		@include md {
			font-size: 15px; }
		@include sm {
			font-size: 14px; }
		&-col {
			display: flex;
			flex-basis: 50%;
			flex-direction: column;
			align-items: flex-start;
			padding: 0 2%;
			&:nth-child(2) {
				padding-left: 55px;
				@include sm {
					padding-left: 30px; } } }
		&-tel {
			margin-bottom: 3px; }
		&-descr {
			margin-bottom: 3px; }
		&-str {
			position: relative;
			margin-bottom: 3px; }
		.icon {
			position: absolute;
			top: 0;
			left: -30px;
			z-index: 1;
			fill: #CFE7F9;
			font-size: 31px;
			@include md {
				font-size: 24px; } } }
	&__copy {
		margin-top: 55px;
		text-align: center;
		color: #CFE7F9;
		font-size: 18px;
		@include sm {
			margin-top: 25px; }
		&-text {
			margin-bottom: 5px;
			@include md {
				font-size: 15px; }
			@include xs {
				font-size: 14px; } }
		&-link {
			color: #CFE7F9;
			text-decoration: underline;
			text-decoration-color: #CFE7F9;
			@include md {
				font-size: 14px; }
			&:hover {
				text-decoration: none; } } } }
