body {
	min-width: 768px;
	@include f("psr", 18, 26);
	color: $black;
	-webkit-font-smoothing: subpixel-antialiased; }

// layout
.out {
	overflow: hidden; }

.container {
	max-width: 1560px;
	margin: 0 auto;
	padding: 0 15px;
	@include sm {
		padding: 0 10px; } }

// titles
h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }

.title-xs {
	@include f("oss",19,30);
	color: $black; }

.title-sm {
	@include f("psb",20); }

//.title-arial
///font-family: Arial, sans-serif
///font-size: 20px
///font-weight: 700
///text-transform: uppercase
///+md
///	font-size: 17px
///+sm
///	font-size: 15px
///+xs
///	font-size: 16px

.title-md {
	padding: 0 30px;
	@include f('psr', 24);
	line-height: 1.25;
	color: #00306B;
	text-transform: uppercase;
	&__color {
		color: #E63833; } }

.title-lg {
	@include f('psr',27);
	color: #00306B;
	text-transform: uppercase; }

// basic transition
@mixin trs {
	transition: .2s cubic-bezier(0.55, 0.09, 0.68, 0.53); }

// mini reset
a {
	text-decoration: none;
	color: inherit; }

img {
	display: block; }

button {
	background: none;
	@include f('psr'); }

