.extra {
	padding: 40px;
	background-color: #f5f8fc;
	@include sm {
		padding: 30px 0; }
	&__container {
		max-width: 1460px;
		@include lg {
			max-width: 1200px; } }
	&__title {
		text-align: center;
		@include md {
			font-size: 18px; }
		@include xs {
			font-size: 16px; } }
	&__inn {
		margin-top: 45px;
		font-size: 0;
		@include lg {
			margin-top: 30px; }
		@include xs {
			margin-top: 20px; } }
	&__text {
		display: inline-block;
		vertical-align: middle;
		width: 35%;
		@include md {
			vertical-align: middle; }
		@include sm {
			width: 40%; }
		@include xs {
			width: 43%; } }
	&__item {
		& + & {
			margin-top: 40px;
			@include md {
				margin-top: 25px; } }
		&-list {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			position: relative;
			padding-left: 110px;
			@include lg {
				padding-left: 65px; } }
		&-icon, &-icon_parts::before {
			position: absolute;
			left: 0;
			top: 50%;
			z-index: 1;
			transform: translateY(-50%);
			fill: #B8BEC4;
			@include md {
				transform: translateY(-53%) scale(.7);
				left: -5px; } }
		&-icon_parts {
			content: '';
			@include s('parts'); }
		.icon-polygraph {
			font-size: 44px; }
		.icon-framing {
			font-size: 83px; }
		&-title {
			padding-left: 110px;
			margin-bottom: 20px;
			@include f("psb",18,26);
			@include lg {
				font-size: 17px; }
			@include md {
				font-size: 15px;
				padding-left: 65px; }
			@include xs {
				margin-bottom: 10px;
				font-size: 14px; } }
		&-product {
			font-size: 18px;
			@include md {
				font-size: 15px; }
			@include xs {
				font-size: 14px; } } }
	&__link {
		font-size: 18px;
		@include lg {
			font-size: 18px; }
		@include md {
			font-size: 16px; }
		@include sm {
			font-size: 14px; } }
	&__links {
		margin-top: 20px;
		padding-left: 110px;
		@include lg {
			padding-left: 65px; } }
	&__pic {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 67%;
		margin-left: -3.5%;
		@include md {
			vertical-align: middle;
			margin-left: -5.5%; }
		@include sm {
			width: 65%; }
		@include xs {
			width: 62%; }
		&::after {
			content: '';
			display: block;
			padding-top: 70.9%; } }
	&__img {
		@include coverdiv;
		@include cover; } }
