/* pictures
	----------------------------------- */
.pictures {
	padding: 0 0 30px;
	&__title {
		text-align: center; }
	&__descr {
		margin: 20px 0;
		text-indent: 40px; }
	&__pics {
		@include clr;
		&-item {
			position: relative;
			float: left;
			padding-top: percentage(344 / 1224);
			&:not(:first-child) {
				margin-left: 0.5%; }
			&:nth-child(1) {
				width: 22.1%; }
			&:nth-child(2) {
				width: 38.3%; }
			&:nth-child(3) {
				width: 38.4%; }
			img {
				@include coverdiv;
				@include cover;
				z-index: 1; } }
		&-label {
			position: absolute;
			left: 0;
			bottom: 0;
			z-index: 1;
			padding: 10px 15px;
			color: #fff;
			font-size: 20px;
			background: rgba(#000, .3);
			text-transform: uppercase;
			&_right {
				right: 0;
				left: auto; } } } }

/* clothes
	----------------------------------- */
.clothes {
	padding: 0 0 40px;
	&__in {
		padding: 20px 0 0;
		@include clr; }
	&__col {
		float: left;
		&:nth-child(1) {
			width: 55%;
			padding-right: 20px; }
		&:nth-child(2) {
			width: 45%; } }
	&__sect-descr {
		&:first-child {
			margin-top: 0; } }
	&__label-block {
		width: 45%; } }

/* news
	----------------------------------- */
.news {
	&__post {
		padding-bottom: 9px;
		border-bottom: 1px solid #D6D9DC;
		@include clr;
		& + & {
			margin-top: 30px; }
		&-pic {
			float: left;
			width: 51%;
			padding-right: 2%;
			margin-bottom: -5px; }
		&-img-wrap {
			height: 340px;
			img {
				width: 100%;
				height: 100%; } }
		&-date {
			padding: 7px 2px;
			@include f('oss', 16);
			color: #BCBFC2;
			span + span {
				margin-left: 5px; } }
		&-title {
			float: right;
			width: 49%;
			padding: 2px 15px;
			margin-bottom: 15px;
			@include f('oss', 20);
			background: #F4F8FD; }
		&-descr {
			position: relative;
			float: left;
			padding: 0 0 30px;
			width: 49%;
			min-height: 265px;
			&.is-open {
				float: none;
				width: auto; }
			p:first-child {
				text-indent: 30px; }
			p:nth-child(2) {
				display: none; }
			&_important {
				font-weight: bold; } }
		&-descr.is-open.news__post-descr_important {
			border-color: transparent; }
		&-more-btn {
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
			bottom: 4px;
			z-index: 1;
			font-size: 18px;
			color: #004D87;
			span {
				display: inline-block;
				vertical-align: middle; }
			&.is-active {
				left: 75%; }
			&.is-active .news__post-more-icon {
				@include rotate(180); } }
		&-more-icon {
			display: inline-block;
			vertical-align: middle;
			font-size: 10px;
			fill: #004D87; } } }

/* machine
	----------------------------------- */
.machine {
	&__in {
		padding: 20px 0;
		@include clr; }
	&__text {
		p {
			text-indent: 30px; }
		p + p {
			margin-top: 20px; } }
	&__pic {
		float: right;
		width: percentage(490 / 1224);
		margin-left: 20px; }
	&__content {
		position: relative;
		clear: both;
		padding: 30px 0;
		&::after {
			content: '';
			display: block;
			height: 114px;
			width: 100%;
			background: url(../img/bg/scissors-decor.svg) no-repeat 50% 50%/cover; }
		&.is-open {
			margin-bottom: 20px; }
		&-in {
			display: none;
			max-width: 860px;
			margin: 0 auto 30px; }
		&-title {
			text-indent: 50px; }
		&-list {
			margin-top: 30px;
			li {
				text-indent: 20px; }
			li:not(:last-child) {
				margin-bottom: 20px; } } }
	&__more-content-open {
		display: inline;
		vertical-align: top;
		margin-left: 4px; }
	&__more-content-close {
		position: absolute;
		left: 50%;
		margin-left: -108px;
		bottom: -20px; } }

/*  payment
	----------------------------------- */
.payment {
	margin: 0 0 50px;
	&__in {
		padding: 20px 0 5px;
		background: #FAFEFF;
		@include clr; }
	&__col {
		padding: 0 4%;
		&:nth-child(1) {
			float: left;
			width: 48%; }
		&:nth-child(2) {
			float: right;
			width: 37%; } }
	&__title {
		margin-bottom: 20px;
		color: #000; }
	&__descr {
		font-size: 19px;
		color: #1A2F4A; }
	&__list {
		@include f('oss', 21);
		li {
			position: relative;
			padding-left: 40px;
			&:not(:last-child) {
				margin-bottom: 5px; } }
		&-icon {
			position: absolute;
			top: -3px;
			left: 0;
			z-index: 1;
			fill: #AEC94D;
			font-size: 32px; } }
	&__bg {
		position: relative;
		min-height: 520px;
		background: #FAFEFF;
		&-img {
			@include coverdiv;
			@include cover;
			z-index: 1; } } }

/* photos
	----------------------------------- */
.photos {
	&__title {
		padding: 0; } }

/* cups
	----------------------------------- */
.cups {
	&__promo {
		margin: 50px 0;
		&-img {
			max-width: 870px;
			margin: 0 auto 35px; } }
	&__package {
		max-width: 1040px;
		margin: 35px auto;
		font-size: 0;
		&-col {
			display: inline-block;
			vertical-align: middle;
			font-size: 19px;
			text-align: center;
			&:nth-child(1) {
				width: 58%; }
			&:nth-child(2) {
				width: 42%;
				padding-left: 3.5%; } }
		&-title {
			margin-bottom: 20px;
			text-align: center;
			font-weight: 700; }
		&-descr {
			text-align: left;
			&:first-of-type {
				text-indent: 30px; }
			&:last-of-type {
				margin-bottom: 20px; } }
		&-row {
			&:not(:last-child) {
				margin-bottom: 5px; }
			@include clr; }
		&-item {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			&:hover .cups__package-zoom {
				visibility: visible;
				opacity: 1; } }
		&-zoom {
			position: absolute;
			top: 6px;
			left: 6px;
			width: 35px;
			height: 35px;
			visibility: hidden;
			opacity: 0;
			line-height: 42px;
			background: rgba(115, 115, 115, 0.6);
			&-icon {
				font-size: 22px;
				fill: #fff; } }
		&-label {
			position: absolute;
			padding: 4px 0;
			width: 100%;
			bottom: 0;
			color: #fff;
			background: rgba(#000, .2);
			@include f('tcm', 13);
			&_font {
				font-size: 11px; }
			&_left {
				padding: 4px 8px;
				max-width: 160px;
				text-align: left; }
			&_right {
				max-width: 100px;
				right: 0; } }
		&-link-decor {
			color: #003399;
			span {
				background-image: linear-gradient(to right, #003399 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #003399 16%, rgba(255, 255, 255, 0) 0%); } } }

	&__text {
		margin: 30px 0;
		&-title {
			text-align: center; } } }

